import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { VestiNaslovna, Vest } from '../../model/dto';

@Component({
  selector: 'app-clanak',
  templateUrl: './clanak.component.html',
  styleUrls: ['./clanak.component.scss']
})
export class ClanakComponent implements OnInit {

  public vest: Vest;

  constructor(
    private router: Router,
    private route: ActivatedRoute) { }

  ngOnInit() {
    let urlParam;
    this.route.params.subscribe((params: Params) => {
      urlParam = params.id;
    });
    this.vest = this.objediniSveVesti()
      .filter((vest: Vest) => vest.id === urlParam)
      .pop();
  }

  idiNazad() {
    this.router.navigate(['/naslovna']);
  }

  objediniSveVesti() {
    const sveVesti = [];
    sveVesti.push(
      this.shell_u_ponudi(), this.febi_u_ponudi(),
      this.mahle_u_ponudi(), this.ks_u_ponudi(),
      this.mahle_pakovanje_vesti(), this.mahle_kompresori_vesti());
    return sveVesti;
  }

  shell_u_ponudi(): Vest {
    const shell_u_ponudi = new Vest();
    shell_u_ponudi.id = 'shell-u-ponudi';
    shell_u_ponudi.naslov = '<h1>Uz novu <b>GTL</b> tehnologiju budunost je poela</h1>';
    shell_u_ponudi.slika = 'assets/slike/novouponudi/velika/shell.png';
    shell_u_ponudi.tekst = '<p>Shell je multinacionalna angloholandska naftna kompanija osnovana osnovana u Ujedinjenom Kraljevstvu sa seditem u Holandiji. Kompanija je vertikalno integrisana i ima aktivnosti gotovo u svim podruijima industrije nafte i gasa. Kao vodea kompanija u proizvodnji ulja I maziva prisutan je u preko 100 zemalja. U svom portfoliu ima preko 2000 proizvoda raznih namena kako bi odgovorio svim zahtevima koje trite diktira. Shell je takoe izbor mnogih proizvoaa vozila, maina, industrijskih postrojenja I mnogih dugih proizvoaa kao prvo punjenje u svojim proizvodima. Shell je trenutno jedina energentska kompanija na svetu koja bazna ulja dobija iz gasa (GTL tehnologija). Postorjenje za preradu se nalazi u Kataru i jedino je tog tipa. Prvo postrojenje se nalazilo u Maleziji I napravljeno je pre 40 godina I sluzilo je za razvoj tehnologije. Razvitkom fabrika je premetena u Katar I sada punim kapacitetima proizvodi potrebne koliine baznog ulja za potrebe svetskog trita. </p> <p>el je takoe I po trinoj kapitalizaciji jedno od vodeih preuzea u svetu. el je u  2013 godini bio vodee preduzee na Fortune global 500-listi najveih preduzea na svetu.</p>';
    return shell_u_ponudi;
  }

  febi_u_ponudi(): Vest {
    const shell_u_ponudi = new Vest();
    shell_u_ponudi.id = 'febi-u-ponudi';
    shell_u_ponudi.naslov = '<h1>Kompletno reenje za vae vozilo</h1>';
    shell_u_ponudi.slika = 'assets/slike/novouponudi/velika/febi.png';
    shell_u_ponudi.tekst = '<p>Febi bilstein je  osnovan davne 1844 godine. Danas je jedan od vodeih svetskih proizvoaa i dobavljaa rezervnih delova za putnika i komercijalna vozila na nezavisnom tritu rezervnih delova.  Vie od 170 godina odrava kontinuitet, kvalitet, uslugu, pouzdanost i inovacije. Asortiman cini 30.000 razlicitih rezervnih delova. Osim toga, febi ima svoja predstavnitva ili iskusnog partnera u preko 69 zemalja. </p> <p>Od upravljake, motorne i kone tehnike, tehnike asija i uvrivanja tokova do autoelektrike i pneumatike i programa febi plus  kod firme febi bilstein ete nai sve za profesionalno servisiranje - putnikih vozila, kamiona, autobusa, prikolica, poluprikolica i malih transportera svih aktuelnih marki i modela.</p> <p class="proba">Febi bilstein stoji za proizvode koji ispunjavaju najvie zahteve kvaliteta izrade i bezbednosti ugradnje. Kvalitetom, brzinom i postavljanjem trendova febi bilstein vam daje odluujui podsticaj na globalnom tritu. Zato to kao premium marka ima obavezu da ispuni zahteve svojih kupaca  svakodnevno.</p> <p>AUTOMATERIJAL d.o.o - je zastupnik koncerna Febi Bilstein i Blue Printa za zapadnu Srbiju.</p>';
    return shell_u_ponudi;
  }

  mahle_u_ponudi(): Vest {
    const mahle_u_ponudi = new Vest();
    mahle_u_ponudi.id = 'mahle-u-ponudi';
    mahle_u_ponudi.naslov = '<h1>U korak sa vremenom</h1>';
    mahle_u_ponudi.slika = 'assets/slike/novouponudi/velika/mahle.png';
    mahle_u_ponudi.tekst = '<p>Mahle je vodei meunarodni partner za razvoj I dobavlja za automobilsku industriju, kao I pionir I pokreta tehnologije za budunost.</p> <p>Zasnovan na velikoj sloenosti sistema koja se sastoji od motora I komponenata motora, filtriranja I upravljanja toplotnom energijom, portfolio proizvoda grupe se bavi svim kljunim pitanjima vezanim za tehnologiju pogonskog sistema i klima ureaja.</p> <p>Mahle proizvodi se ugrauju u najmanje svako drugo vozilo irom sveta. Decenijama Mahle komponente I sistemi su takoe korieni na svetskim trkalitima I izvan puta-u stacionarnim aplikacijama, za mobilnu maineriju, elezniku transport, kao I pomorske aplikacije.</p> <p>Ono po emu je Mahle preopoznatljiv irom sveta je svakako pre svega motorna grupa. Ogroman deo svetskih proizvoaa vozila, kako  putnikih tako I teretnih upravo ugrauju u svoje motore komponente dizajnirane u napravljene u Mahle fabrikama. Takoe, kupovinom svetskog Brenda Knecht, Mahle ulazi kao premijum proizvoa filtera na trite, nudei svoje proizvode u aftermarketu istog kvaliteta kao I oni koji su prva ugradnja ovog proizvoaa.</p> <p>Kupovinom nekoliko velikih proizoaa komponenti za vozila Mahle je svoj portfolio proirio I na ureaje vezane za klimatizaciju vozila, termomenadment, turbopunjae, alternatore, alnasere I mnoge druge sastavne delove vozila. Ono po emu je mahle prepoznatljiv je motorna grupa.</p>';
    return mahle_u_ponudi;
  }

  ks_u_ponudi(): Vest {
    const shell_u_ponudi = new Vest();
    shell_u_ponudi.id = 'ks-u-ponudi';
    shell_u_ponudi.naslov = '<h1>Godine poverenja su preporuka</h1>';
    shell_u_ponudi.slika = 'assets/slike/novouponudi/velika/ks.png';
    shell_u_ponudi.tekst = '<p>U svojoj ulozi dobavljaa prvog reda, Pierburg je tradicionalno bio jedan od najbliih partnera automobilskoj industriji, od samog poetka uspenog unapreenja razvoja automobila.Pierburg je osnovan 1909. godine kao trgovac elikom u Berlinu; 1928. godine, Pierburg je poeo s proizvodnjom karburatora i vrlo brzo je stsao u glavnog  dobavljaa za sve nemake proizvoae automobila i mnoge meunarodne proizvoae motornih vozila i proizvoae motora.Godine 1986. Pierburg je preuzeo Rheinmetall Group i 1998. se spojio sa Kolbenschmidt-om da bi formirao Kolbenschmidt Pierburg AG.U okviru Rheinmetall Automotive, Pierburg je specijalista za kontrolu emisije, dovod vazduha, prigune ventile i solenoidne ventile.Decenije iskustva u kombinaciji sa sveobuhvatnim, inovativnim i globalno priznatim mogunostima u svakom aspektu motora su faktori koji su doveli Pierburg u svoju misiju da stalno razvija i proizvodi komponente, module i sisteme za budunost.U razvoju novih generacija motora dobijaju na znaaju takvi faktori kao to su smanjena potronja goriva, nie emisije tetnih gasova  i poboljane performanse, udobnost i sigurnost.U te svrhe, Pierburg nudi visoko tehnoloka reenja: elektrine pumpe za hlaenje na zahtev, sisteme za recirkulaciju izduvnih gasova  sa istosmernim motorom, ventile za odvod vazduha, te razliite verzije pogonskih modula. Svi ovi razvoji pomau da se stvori ekonomski i ekoloki uravnoteen automobil.</p> <p>Divizija KS Kolbenschmidt razvija, proizvodi i prodaje klipove za benzinske i dizel motore za automobile i komercijalna vozila i jedan je od najistaknutijih igraa u svom segmentu. Ostali proizvodi ukljuuju klipove za dvotaktne i kompresorske motore kao i velike klipove za stacionarne, brodske i lokomotivne motore.Unapreenje gustine snage, kao i postizanje daljnjeg smanjenja potronje goriva i emisija, ve godinama predstavljaju faktore koji utiu na razvoj novih tipova klipova kod KS Kolbenschmidt.</p>';
    return shell_u_ponudi;
  }

  mahle_pakovanje_vesti() {
    const mahlePakovanjeVesti = new Vest();
    mahlePakovanjeVesti.id = 'mahle-pakovanje';
    mahlePakovanjeVesti.naslov = '<h1>Nova ambalaa - dokazan kvalitet</h1>';
    mahlePakovanjeVesti.slika = 'assets/slike/novouponudi/velika/mahle-pakovanja.png';
    mahlePakovanjeVesti.tekst = '<p>MAHLE Aftermarket proizvodi dolaze u novo dizajniranom pakovanju od 2019. godine. Jednostavno i jasno za lako rukovanje u trgovini i radionicama. I naravno, oni e jo uvek imati dokazan kvalitet MAHLE.</p> <p>Novo: Jasne boje, koncizna informacija i jasan dizajn omoguavaju lako rukovanje i brzu identifikaciju u skladitu.</p> <p>Novi pristup opisu proizvoda prilagoen itau, dodatne ikone podrke za proizvode, prikazuju se samo bitne informacije.</p><p>Takoe je usklaena ema boja, to je rezultiralo sledeim promjenama:</p> <ul><li>Promena boje ambalae BEHR sa narandaste na plavu</li><li> Promena boje za delove motora od crvene do plave</li><li> inimo ga jo jednostavnijim: MAHLE ORIGINAL postaje MAHLE</li></ul> <p> Jednostavno i jasno - zahvaljujui novom dizajnu ambalae: </p><ul> <li>Svi relevantni detalji na prvi pogled</li> <li>Jednostavno rukovanje u trgovini i radionicama</li> <li>Jednostavna identifikacija u skladitu (sa udaljenosti) zahvaljujui jasnim bojama</li> <li>Garantovana visoka sigurnost (zatita od piraterije proizvoda)</li> <li>Snaan izgled za sedam jakih brendova</li></ul> <p>MAHLE Aftermarket objedinjuje sedam jakih, meunarodnih brendova koji su sinonim za inovacije i beskompromisni kvalitet. Pored kiobranskog brenda MAHLE, tu spadaju uspene kompanije kao to su BEHR, CLEVITE, IZUMI, METAL LEVE i KNECHT FILTER. BRAIN BEE, novo integrisana u MAHLE porodicu marki 2018. godine, je strunjak za radioniku opremu i dijagnostiku za trita u Evropi, Bliskom Istoku, Africi, Junoj Americi i delovima Azije.</p>  <p>irom svijeta, brand MAHLE je sinonim za rezervne delove u kvalitetui originalne opreme du celog pogonskog sklopa - od komponenti motora, turbokompresora, filtera, hlaenja motora i klima ureaja, starter motora i alternatora kroz radioniku opremu i dijagnostiku, e-mobilnost i elektroniku , pa ak i rezervne delove za klasina vozila.</p>';
    return mahlePakovanjeVesti;
  }

  mahle_kompresori_vesti() {
    const mahleKompresoriVesti = new Vest();
    mahleKompresoriVesti.id = 'mahle-kompresori';
    mahleKompresoriVesti.naslov = '<h1>Mahle kompresori klime</h1>';
    mahleKompresoriVesti.slika = 'assets/slike/novouponudi/velika/kompresori.png';
    mahleKompresoriVesti.tekst = '<p>Kompanija MAHLE ulazi na trite kompresora klima ureaja. Sinonim kvaliteta i pouzdanosti kompanije MAHLE  ovi kompresori opravdavaju.</p> <p>Nivo klimatskog komfora definisan u dananjim vozilima je ve na visokom nivou i nastavie da raste u budunosti. Meutim, klima ureaji imaju vie uloga nego to je  udobna vonja. Oni takoe igraju kljunu ulogu u pogledu bezbednosti. Pored odravanja vozaa udobnim i opreznim, klima ureaj takoe odvlauje vazduh u kabini i na taj nain spreava zamagljivanje prozora.</p> <p>U svom portfoliu MAHLE nudi veliki broj kompresora za veliki broj renomiranih proizvoaa vozila, kao to su VW, AUDI, BMW, Mercedes, Opel...</p>';
    return mahleKompresoriVesti;
  }

}
