import { Component, OnInit, HostListener } from '@angular/core';
import { Brend } from '../model/dto';
import { MatDialog } from '@angular/material';
import { BrendoviModalComponent } from 'src/app/shared/modal/brendovi-modal/brendovi-modal.component';
import { e } from '@angular/core/src/render3';

@Component({
  selector: 'app-o-nama',
  templateUrl: './o-nama.component.html',
  styleUrls: ['./o-nama.component.scss']
})
export class ONamaComponent implements OnInit {
  innerWidth;
  public mySlideBrands = this.vratiSveBrendove();
  public mySlideOptions = null;

  constructor(public dialog: MatDialog) { }

  ngOnInit() {
    this.innerWidth = window.innerWidth;
    this.changeSlideConfiguration();
  }

  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.innerWidth = window.innerWidth;
    this.changeSlideConfiguration();
  }

  changeSlideConfiguration() {
    if (this.innerWidth < 500) {
      this.mySlideOptions = {
        items: 3, margin: 5,
        dots: true, nav: false, autoplay: true,
        autoplayTimeout: 2500, rewind: true
      };
    } else {
      this.mySlideOptions = {
        items: 7, margin: 20,
        dots: true, nav: false, autoplay: true,
        autoplayTimeout: 2500, rewind: true
      };
    }
  }

  otvoriDialog(brend: Brend) {
    this.dialog.open(BrendoviModalComponent, {
      width: '700px',
      data: brend
    });
  }

  vratiSveBrendove() {
    const nizOdBrendova = [];
    const febi = this.febi();
    const shell = this.shell();
    const mahle = this.mahle();
    const kolbenschmidt = this.kolbenschmidt();
    const pierburg = this.pierburg();
    const victorReinz = this.victorReinz();
    const bluePrint = this.bluePrint();
    const mm = this.mm();
    nizOdBrendova.push(febi, shell, mahle, victorReinz, kolbenschmidt, pierburg, mm, bluePrint);
    return nizOdBrendova;
  }

  febi() {
    const febi = new Brend();
    febi.ime = 'Febi';
    febi.urlSlikeLogo = 'assets/slike/brendovi/logo/febi.png';
    febi.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/febi.png';
    febi.opis = '<p>Febi bilstein je  osnovan davne 1844 godine. Danas je jedan od vodeih svetskih proizvoaa i dobavljaa rezervnih delova za putnika i komercijalna vozila na nezavisnom tritu rezervnih delova.  Vie od 170 godina odrava kontinuitet, kvalitet, uslugu, pouzdanost i inovacije. Asortiman cini 30.000 razlicitih rezervnih delova. Osim toga, febi ima svoja predstavnitva ili iskusnog partnera u preko 69 zemalja. </p> <p>Od upravljake, motorne i kone tehnike, tehnike asija i uvrivanja tokova do autoelektrike i pneumatike i programa febi plus  kod firme febi bilstein ete nai sve za profesionalno servisiranje - putnikih vozila, kamiona, autobusa, prikolica, poluprikolica i malih transportera svih aktuelnih marki i modela.</p> <p class="proba">Febi bilstein stoji za proizvode koji ispunjavaju najvie zahteve kvaliteta izrade i bezbednosti ugradnje. Kvalitetom, brzinom i postavljanjem trendova febi bilstein vam daje odluujui podsticaj na globalnom tritu. Zato to kao premium marka ima obavezu da ispuni zahteve svojih kupaca  svakodnevno.</p> <p>AUTOMATERIJAL d.o.o - je zastupnik koncerna Febi Bilstein i Blue Printa za zapadnu Srbiju.</p>';
    return febi;
  }

  shell() {
    const shell = new Brend();
    shell.ime = 'Shell';
    shell.urlSlikeLogo = 'assets/slike/brendovi/logo/shell.png';
    shell.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/shell.png';
    shell.opis = '<p> Kompanije Shell ima iza sebe 70 godina u inovacijama i stoga je broj jedan dobavlja lubrikanata. Poznata injenica je da neki od  svetskih proizvoaa automobila preferiraju Shell motorno ulje prvo punjenje novih vozila koja izlaze iz fabike. Kompletan asortiman ukljuujui sintetike proizvode visokih performansi moe da zadovolji sve poterbe i aplikacije koje vam trebaju.</p><p>AUTOMATERIJAL d.o.o je ovlaceni distributer Shell ulja. </p>'
    return shell;
  }

  victorReinz() {
    const vr = new Brend();
    vr.ime = 'Victor Reinz';
    vr.urlSlikeLogo = 'assets/slike/brendovi/logo/vik.png';
    vr.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/victor-reinz.png';
    vr.opis = '<p> Victor Reinz je Nemaka kompanija koja donosi inovativne tehnologije zaptivanja vrhunskog kvaliteta. Vie od 80 godina brend Victor Reinz je sinonim za vrhunske zaptivae i semeringe. Svi rezervni delovi u asortimanu ispunjavaju visoke standarde kvaliteta OEM. Poslednjih godina na tritu su zauzeli vodeu poziciju, kao proizvoai dihtunga glave, garniture dihtunga i semeringa.</p><p > Skoro sve poznate marke i modeli proizvoaa automobila irom sveta su opremljeni sa profesionalnim zaptivnim reenjima iz Victor Reinza u oblasti motora i izduvnih grasova.</p>'
    return vr;
  }

  mahle() {
    const mahle = new Brend();
    mahle.ime = 'Mahle';
    mahle.urlSlikeLogo = 'assets/slike/brendovi/logo/mahle.png';
    mahle.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/mahle.png';
    mahle.opis = '<p>Marka MAHLE oznaava rezervne delove u kvalitetu originalne opreme du celog pogonskog sklopa - od komponenti motora, filtracije i perifernih ureaja motora do upravljanja toplotom i mehatronike. Takoe predstavlja visokokvalitetnu specijalnu opremu za odravanje i servis. </p> <p>Marka je podrana od strane MAHLE-a, vodeceg svetskog dobavljaa automobilske opreme, koji snabdeva trite automobilske opreme visokokvalitetnim proizvodima zahvaljujuci OE kompetenciji, vodeci istraivaki i razvojni rad, kao i svoju meunarodnu logistiku mreu. </p>'
    return mahle;
  }

  kolbenschmidt() {
    const kolbenschmidt = new Brend();
    kolbenschmidt.ime = 'Kolbenschmidt';
    kolbenschmidt.urlSlikeLogo = 'assets/slike/brendovi/logo/ks.png';
    kolbenschmidt.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/ks.png';
    kolbenschmidt.opis = '<p> Kolbenschmidt razvija i proizvodi klipove za benzin i dizel motore za putnicka i teretna vozila. Jedan je od najistaknutijih igraca u ovom segmentu. Ostali proizvodi ukljuuju ventile, voice ventila, karike, leajeve radilica, bregaste osovine.. </p> <p class="probam">Smanjenje potronje goriva i emisija izduvnih gasova su godinama glavni pokreta razvoja  novih vrsta klipova.</p> <p class="proba"> Ve godinama, mnogi vani dileri originalnih delova su redovne muterije Kolbenschmidt-a. Glavna trita su Evropa, Serverna i Juna Amerika, i Azija.';
    return kolbenschmidt;
  }

  pierburg() {
    const pierburg = new Brend();
    pierburg.ime = 'Pierburg';
    pierburg.urlSlikeLogo = 'assets/slike/brendovi/logo/pirb.png';
    pierburg.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/pb.png';
    pierburg.opis = '<p>Pierburg, kao dobavlja prve klase, tradicionalno je jedan od najbliih partnera u autobomilskoj industriji. Osnovan je 1909. godine, kao trgovina elika u Berlinu. 1928. godine poinje sa proizvodnjom karburatora i uskoro postaje jedini snabdeva svih nemakih automobila i mnogih svetskih proizvoaa motornih vozila i proizvoaa motora. Pierburg razvija inovativne komponente i sistemska reenja sa priznatim nadlenostima u oblasti dovoda vazduha i kontrole emisija. Svi ovi proizvodi, pumpe goriva, egr ventil, nepovratni ventil, protokomeri o vakuum pumpe, odgovaraju visokim zahtevima trita i standardima kvaliteta.</p> <p> Kako u prolosti tako i sad, Pierburg aktivno oblikuje budunost automobila.</p>';
    return pierburg;
  }

  mm() {
    const mm = new Brend();
    mm.ime = 'Magenti Marreli';
    mm.urlSlikeLogo = 'assets/slike/brendovi/logo/mm.png';
    mm.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/magneti-marelli.png';
    mm.opis = '<p>Magneti Mareilli je meunarodna grupa posveena projektovanju i proizvodnji sistema visoke tehnologije i komponenti za automobilsku indrustriju. Osnovana je 1919. godine, sa kapitalom potpisana na dva jednaka dela izmeu Fiat-a i kompanije Ercole Marelli. Sa 38.000 zaposlenih, 89 proizvodnih pogona, prisutni su u 19 zemalja i snabdevaju sve najvanije proizvoae u Evropi, Severnoj i Junoj Americi i Aziji. Cilj Magenti Marelli-ja je da bude na raspolaganju kupcima po pristupanoj ceni, spajanjem visokog kvaliteta i visoke tehnologije.</p> <p> Magneti Marelli je prvi stvorio ksenonsko svetlo 1991. godine, dajui novu dimenziju frontalnoj rasvetli. Od 2010. godine, postaje dobavlja Mercedes-Benz za sve LED farove sa prilagodljivom funkcijom za svetlo.</p> <p>2010. godine Ministartsvo za kulturnu batinu i aktivnost Italije nagrauje Magneti Marelli sa "Deklaracija o istorijskom znaaju".</p>';
    return mm;
  }

  bluePrint() {
    const bp = new Brend();
    bp.ime = 'Blue Print';
    bp.urlSlikeLogo = 'assets/slike/brendovi/logo/bp.png';
    bp.urlSlikePozadina = 'assets/slike/brendovi/pagepaper/blueprint.png';
    bp.opis = '<p>Blue Print je pravi izbor za kvalitetne rezervne delove za azijske, amerike, britanske automobile i laka komercijalna vozila. Blue Print prua aftermarket visoko preciznim komponentama kao i tehnikim i informacijskim reenjima za vie od 160 razliitih tipova proizvoda.</p> <p>irok asortiman od vie od 23.000 artikala dostupnih u preko 160 vrsta proizvoda pod jednom robnom markom, ukljuujuci i OE proizvode svrstava Blue Print kao jednog od brendova sa najveom ponudom na tritu.</p>';
    return bp;
  }
}
